	import {
		login,
		ajax,
		formatDate
	} from '@/static/js/base.js'
	import api from '@/static/js/api.js'
	import MescrollEmpty from '@/components/mescroll-uni/components/mescroll-empty.vue'
	import {
		mapGetters,
		mapMutations
	} from 'vuex'
	export default {
		components: {
			MescrollEmpty
		},
		data() {
			return {
				URL: '',
				IMG_URL: '',
				
				bannerArr: [],
				classifyArr: [],
				demandArr: [],
				upOption: {
					textNoMore: '木有更多了', // 没有更多数据的提示文本
					empty: {
						tip: '~ 暂无数据 ~', // 提示
					}
				},
				purchasesList: []
			}
		},
		onLoad(options) {
			var that = this
			that.URL = api.URL
			that.IMG_URL = api.IMG_URL
			// uni.getSetting({
			// 	success(res) {
			// 		if (res.authSetting['scope.userInfo']) {
			// 			uni.getUserInfo({
			// 				success(res) {
			// 					that.setUserInfo(res.userInfo)
			// 					// that.$store.commit('setUserInfo', res.userInfo)
			// 					login()
			// 				}
			// 			})
			// 		}
			// 	}
			// })
      // let token = ''
      // if (token) {
        
      // }
      that.login()
			// ajax({
			// 	url: 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx9a4e35a5462738c8&redirect_uri=https://xtn.beaconway.cn/api/auth/login&response_type=code&scope=snsapi_userinfo&state=1#wechat_redirect',
			// 	type: "GET"
			// }).then(res => {
			// 	if (res.status_code == "ok") {
					
			// 	}
			// })
			// 首页banner请求
			ajax({
				url: api.index_banner,
				type: "GET"
			}).then(res => {
				if (res.status_code == "ok") {
					let bannerArr = res.data
					for (let i = 0; i < bannerArr.length; i++) {
						bannerArr[i].banner = `${that.IMG_URL}/${bannerArr[i].banner}`
					}
					this.bannerArr = bannerArr
				}
			})
			// 求购分类推荐入口
			ajax({
				url: api.home_push,
				type: "GET"
			}).then(res => {
				if (res.error_code == 4000) {
					this.classifyArr = res.data
				}
				if (res.status_code == "ok") {
					let classifyArr = res.data
					for (let i = 0; i < classifyArr.length; i++) {
						classifyArr[i].icon = `${that.IMG_URL}/${classifyArr[i].icon}`
					}
					this.classifyArr = classifyArr
				}
			})
			// 求购分类默认入口
			ajax({
				url: api.home_plain,
				type: "GET"
			}).then(res => {
				console.log(res)
				if (res.status_code == "ok") {
					let demandArr = res.data
					for (let i = 0; i < demandArr.length; i++) {
						demandArr[i].icon = `${that.IMG_URL}/${demandArr[i].icon}`
					}
					console.log(demandArr)
					this.demandArr = demandArr
				}
			})
		},
		computed: {
			...mapGetters(['userInfo']),
		},
		methods: {
      login() {
          // window.location.href = "https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx9a4e35a5462738c8&redirect_uri=https://xtn.beaconway.cn/api/auth/login&response_type=code&scope=snsapi_userinfo&state=1#wechat_redirect";
      },
			getphonenumber(e) {
				console.log(e)
			},
			...mapMutations(['setUserInfo']),
			getUserInfo(e) {
				if (e.detail.errMsg == 'getUserInfo:ok') {
					this.setUserInfo(e.detail.userInfo)
					login()
				}
			},
			upCallback(mescroll) {
				this.getPurchases(mescroll, curPageData => {
					if (mescroll.num == 1) this.list = curPageData; //如果是第一页需手动制空列表
					this.purchasesList = this.purchasesList.concat(curPageData)
					this.$nextTick(() => {
						mescroll.endSuccess(curPageData.length);
					})
				})
			},
			// 获取求购列表
			getPurchases(mescroll, cb) {
				ajax({
					url: api.home_news,
					type: "GET",
					data: {
						number: 10,
						page: mescroll.num,
					}
				}).then(res => {
					console.log(res)
					var purchasesList = res.data.data || []
					// purchasesList.forEach(item => {
					// 	item.time = formatDate(item.ctime)
					// })
					cb(purchasesList)
				})
			},
			postWant(){
				uni.navigateTo({
					url:'../postWant/postWant'
				})
			}
		}
	}
